<template>
    <div class="ddd">
        <div class="mvideo">
            <span class="back iconfont icon-tuichu"  @click="back"></span>
            <video :src="mvurl" controls autoplay style="width= 100%; height=100%; object-fit: fill"></video>
        </div>
    </div>
</template>

<script>
export default {
    name:"mv",
    data(){
        return{
            mvId:null,
            mvurl:null,
        }
    },
    methods:{
        // 返回上一级
        back(){
            this.$router.go(-1);
        },
    },
    created(){
        this.mvId = this.$route.params.id;
        this.$axios.get('/mv/url?id=' + this.mvId).then(data=>{
            this.mvurl = data.data.data.url;
        }).catch(err=>{
            console.log(err);
        });
    }
}
</script>

<style lang="less" scoped>
.mvideo{
        width: 100vw;
        height: 100vh;
        position: relative;
        video{
            width: 100%;
            height: 100%;
        }
        .back{
            position: absolute;
            top: 14px;
            right: 0px;
            margin: 0px 10px;
            font-size: 30px;
            color:white;
            z-index: 999;
        }
}
</style>